*,
*::after,
*::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  box-sizing: inherit;
  /* tornamos ele herdavel e no body setamos o default - é uma boa prática */
}

html {
  font-size: 62.5%;
  /* por padrão p browser vem com 16px, nos queremos colocar 10px no default para faciliartar nossos cálculos, logo 10/16 = 0.625  */
}

body {
  box-sizing: border-box;
}

body {
  font-family: sans-serif;
}

.header__title {
  font-size: 3.6rem;
  text-align: center;
  line-height: 1;
}

.header__call {
  color: #9b9b9b;
  font-size: 1.8rem;
  line-height: 1.5;
}

.form__header-title {
  font-size: 2.4rem;
  line-height: 1.08;
  text-align: center;
  font-family: 'Circular Std Bold', sans-serif;
}

.form__forgot-password-block {
  font-family: CircularStd;
  font-size: 1.2rem;
  font-weight: bold;
}

.btn {
  font-size: 1.4rem;
}

.footer {
  font-size: 1.4rem;
}
.form__header {
  margin: 0 auto;
  background-color: #ffffff;
  width: 90%;
  border-radius: .5rem;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
  padding: 3.5rem 3rem;
}

@media (min-width: 37.5em) {
  .form__header {
    width: 49rem;
  }
}

.form__header-title {
  color: #000000;
  margin: 0 2.5rem 2.5rem;
  color: #4a4a4a;
}

.form__group:not(:last-child) {
  margin-bottom: 1.5rem;
}

.form__input {
  border-radius: 5px;
  border: solid 1px #d6d9e0;
  background-color: #ffffff;
  padding: .8rem .5rem .8rem .5rem;
  width: 100%;
  outline: none;
}

.form__action {
  margin-top: 2.8rem;
  display: flex;
  justify-content: space-around;
}

.form__forgot-password-block {
  color: #bbbec4;
  margin-top: 3.2rem;
}

.u-flex-grow {
  flex-grow: 1;
}

.btn {
  border-radius: 30rem;
  border: none;
  padding: 1.5rem 1rem;
  color: #ffffff;
}

.btn--facebook {
  background-color: #3a589b;
}

.btn--primary {
  background-color: #00bc9b;
  width: 180px;
}

.btn--facebook.u-flex-grow {
  margin-right: 1.5rem;
}

.form-error {
  color: red;
  margin-top: .5rem;
}
.img-responsive {
  width: 100%;
}

.footer {
  background-color: #f6f7fb;
  color: #8e929f;
  padding: 3.8rem 5rem;
}

.footer__logo-block {
  display: flex;
  margin-bottom: 2rem;
}

.footer__logo {
  color: #000000;
  font-weight: bold;
}

.footer__copyrights {
  padding: 0 1rem 0 3rem;
}

.footer__nav-block {
  display: flex;
}

.footer__nav {
  margin: 0 0 0 auto;
}

.footer__nav .bullet-separator {
  margin: 0 .7rem;
}

.footer__nav-link,
.footer__nav-link:link {
  color: #8e929f;
  text-decoration: none;
  margin-bottom: 1rem;
}

.footer__nav-link:hover {
  color: #4a4a4a;
}

@media (min-width: 37.5em) {
  .footer {
    display: flex;
    align-items: center;
    justify-content: space-around;
  }
  .footer__logo {
    width: 16rem;
    margin-right: 3rem;
  }
  .footer__copyrights {
    margin: auto 1rem;
    width: 25rem;
    line-height: 1.5;
  }
}
.header {
  background-image: -webkit-linear-gradient(339deg, #00bc9b, #1696e1);
  background-image: linear-gradient(111deg, #00bc9b, #1696e1);
  background-repeat: no-repeat;
  background-size: 100% 65vh;
  background-position: top;
  height: 90vh;
  color: #ffffff;
  margin-bottom: 5rem;
  position: relative;
}

.header__title {
  padding: 2.5rem;
}

.header__form-block {
  widows: 80%;
}

.header__call {
  width: 90%;
  margin: 2rem auto 0;
}

@media (min-width: 37.5em) {
  .header {
    background-size: 100% 60vh;
  }
}

@media (min-width: 58em) {
  .header {
    background-size: 100% 65vh;
    padding: 18.5rem 3.6rem 0 0;
  }
  .header__title-block {
    float: left;
    width: 45%;
  }
  .header__title {
    width: 40rem;
    padding-top: 10rem;
    text-align: left;
    margin: 0 auto;
  }
  .header__form-block {
    float: right;
  }
  .header__call {
    clear: left;
    padding-top: 3rem;
    margin-left: 3rem;
    width: 42%;
    line-height: 1.8;
  }
}

@media (min-width: 75em) {
  .header__call {
    padding-top: 5rem;
  }
}

@media (min-width: 85em) {
  .header__call {
    padding-top: 10rem;
  }
}
.welcome-page__header {
  background-image: -webkit-linear-gradient(339deg, #00bc9b, #1696e1);
  background-image: linear-gradient(111deg, #00bc9b, #1696e1);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top;
  height: 15vh;
  color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
}

.welcome-page__main {
  height: 60vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10rem;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
}

.welcome-page__main-title {}
